<template>
  <div class="container">
    <h2>为您推荐</h2>
    <div class="list clearfix">
      <div class="item" v-for="(item,index) in listData" :key="index" @click="goDetalis(item.pid)">
        <div class="img img_auto">
          <img :src="item.smallImg" alt class="img_auto" />
        </div>
        <span class="span1">{{item.name}}</span>
        <span class="span2">￥{{item.price}}</span>
      </div>
       <h2 class="h2">到底拉</h2>
    </div>
   
  </div>
</template>

<script>

export default {
  data() {
    return {
      listData: []
    };
  },

  created() {
    let words=['latte',"coffee","rena_ice"]
    words.sort((a,b)=>{
        return 0.5-Math.random()
    })
    
    this.getRecomment(words[0]);
    this.getRecomment(words[1]);
   
  },

  methods: {
      goDetalis(pid){
        
        
      this.$router.push({path:'/detalis',query:{
          pid
        }})
      },
    getRecomment(value) {
      this.axios({
        method: "get",
        url: "/typeProducts",
        params: {
          appkey: this.appkey,
          key: 'type',
          value: value
        }
      })
        .then(res => {
          
          if (res.data.code == 500) {
            let data = res.data.result;
            this.listData.push(...data);

            
            if(this.listData.length%2==1){
              this.listData.splice(-1,1)
            }
            
          }
        })
        .catch(err => {
          
        });
    }
  }
};
</script>

<style lang="less" scoped>
h2 {
  text-align: center;
  color:#212121;
}
.list {
  width: 95%;
  margin: 0 auto;
  padding-bottom: 60px;
  &:nth-child(odd) .item {
    float: left;
  }
  &:nth-child(even) .item {
    float: right;
  }
  .item {
    width: 50%;
    text-align: center;
    .img {
      width: 85%;
      display: block;
      margin: 0 auto;
      .img_auto{
         border-radius: 20px;
      }
    
    } 
    span{
       display: block;
       margin: 8px 0;

     }
     .span1{
       font-weight: bold;
     }
  }
  .h2{
    color: #707070;
  }
}
</style>